.question-input-form {
  display: flex;
  padding: 10px;
  border: 1px solid #e0e0e0;
  border-radius: 40px;
  width: 750px;
}

.question-input-field {
  flex-grow: 1;
  padding: 10px;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  font-family: "Google Sans Flex", sans-serif;
  font-size: 16px;
}

.question-input-field:focus {
  outline: none;
  border-color: var(--primary-color);
}

.question-input-button {
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  cursor: pointer;
  font-size: 16px;
  border-radius: 100px;
}

.question-input-button:hover {
  background-color: #0c969e;
}