.sidebar {
  background-color: #f9f9f9;
  border-right: 1px solid #e0e0e0;
  height: 100vh;
  overflow-y: auto;
  transition: width 0.3s ease-in-out;
  position: fixed;
  left: 0;
  top: 0;
  padding-top: 60px;
  z-index: 10;
}

.sidebar.closed {
  width: 60px; /* Width when closed */
  padding: 1rem 0.5rem;
}

.sidebar-header {
  display: flex;

  align-items: center;
  width: 100%;
}

.sidebar.open {
  width: 250px; /* Width when open */
  padding: 1rem;
}

.sidebar-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.sidebar-title {
  font-family: "Google Sans Flex", sans-serif;
  font-size: 0.9rem;
  font-weight: 400;
  margin-bottom: 1rem;
  color: #5d5d5d;
}

.question-history {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  margin-top: 1.5rem;
}

.new-chat-btn {
  background: none;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  cursor: pointer;
  font-size: 16px;
  border-radius: 100px;
  margin-bottom: 1rem;
}

.question-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
}

.question-item {
  margin-bottom: 0.5rem;
}

.question-item button {
  background: none;
  border: none;
  color: #585858;
  font-family: "Google Sans Flex", sans-serif;
  font-size: 1.1rem;
  font-weight: 400;
  cursor: pointer;
  padding: 0;
}

.question-item button:hover {
  color: var(--primary-color);
  text-decoration: underline;
}


.sidebar-footer {
  width: 100%;
  padding: 1rem;
  border-top: 1px solid #e0e0e0;
  margin-top: auto; /* Pushes the footer to the bottom */
}

.account-btn {
  width: 100%;
  padding: 0.75rem 1rem;
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}

.account-btn:hover {
  background-color: #0c969e;
}
